.section {
    padding: 40px 0 50px 0;

    &__title {
        font-size: 30px;
        margin-top: 0;
        margin-bottom: 40px;
        line-height: 1.2;
        letter-spacing: .05em;

        &--center {
            text-align: center; } }

    &__text {
        display: block;
        font-size: 14px;
        color: var(--accent-color);
        text-transform: uppercase;
        margin-bottom: 10px;

        &--center {
            text-align: center; } } }
