.header {
    border-bottom: 1px solid #e9e9e9;

    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 120px;
        padding: 20px 0;

        @media (max-width: 650px) {
            display: none; } }

    &__nav-menu {
        height: 100%;

        &-list {
            display: flex;
            margin: 0;
            padding: 0;
            list-style: none; }

        &-item:not(last-child) {
            margin-right: 20px; }

        &-link {
            color: var(--main-color);
            text-decoration: none;

            &:hover {
                color: var(--accent-color); } } } }

.logo {
    display: flex;
    text-decoration: none;
    color: inherit;

    &__img-wrapper {
        width: 70px;
        height: 70px;
        margin-right: 20px; }

    &__img {
        object-fit: cover;
        width: 100%;
        height: 100%; }

    &__text {
        display: flex;
        flex-direction: column;
        justify-content: center; }

    &__name {
        margin: 0;
        font-size: 22px; }

    &__desc {
        font-size: 11px;
        text-transform: uppercase; } }
