@import url(vendor/normalize.css);
@import url(vendor/simple-grid.css);
:root {
  --main-color: #081d47;
  --accent-color: #db3120;
  --background-color: #fff;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Thin.woff2") format("woff2");
  font-weight: 100;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  font-family: "Roboto";
}

body {
  color: var(--main-color);
  background-color: var(--background-color);
}

.site-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;
}

.container-fluid {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
}

.header {
  border-bottom: 1px solid #e9e9e9;
}

.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 120px;
  padding: 20px 0;
}

@media (max-width: 650px) {
  .header__content {
    display: none;
  }
}

.header__nav-menu {
  height: 100%;
}

.header__nav-menu-list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

.header__nav-menu-item:not(last-child) {
  margin-right: 20px;
}

.header__nav-menu-link {
  color: var(--main-color);
  text-decoration: none;
}

.header__nav-menu-link:hover {
  color: var(--accent-color);
}

.logo {
  display: flex;
  text-decoration: none;
  color: inherit;
}

.logo__img-wrapper {
  width: 70px;
  height: 70px;
  margin-right: 20px;
}

.logo__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.logo__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.logo__name {
  margin: 0;
  font-size: 22px;
}

.logo__desc {
  font-size: 11px;
  text-transform: uppercase;
}

.main {
  flex: 1 1 auto;
}

.section {
  padding: 40px 0 50px 0;
}

.section__title {
  font-size: 30px;
  margin-top: 0;
  margin-bottom: 40px;
  line-height: 1.2;
  letter-spacing: .05em;
}

.section__title--center {
  text-align: center;
}

.section__text {
  display: block;
  font-size: 14px;
  color: var(--accent-color);
  text-transform: uppercase;
  margin-bottom: 10px;
}

.section__text--center {
  text-align: center;
}

.link {
  text-decoration: none;
  color: inherit;
}

.img {
  display: block;
}

.img-wrapper--sm {
  height: 250px;
}

.img-wrapper--md {
  height: 450px;
}

.img-wrapper--lg {
  height: 550px;
}

.img--flexible {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.vh {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.about__item {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.about__title {
  margin-top: 0;
  margin-bottom: 20px;
}

.about__text {
  display: block;
  flex: 1 1 auto;
  margin-top: 0;
  margin-bottom: 20px;
}

.about__link {
  display: block;
}
